.stats__wrapper {
  width: auto;
  max-width: 20rem;
  margin: 0 auto; }

.stats__header {
  padding-top: 1.875rem;
  padding-right: 2.5rem;
  padding-bottom: 1.5625rem;
  padding-left: 2.5rem;
  color: #000000;
  background-color: #ffffff; }

.stats__slogan {
  display: block;
  margin-bottom: 0.9375rem;
  font-size: 1.875rem;
  line-height: 1.875rem;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase; }

.stats__intro {
  margin: 0;
  font-size: 1.25rem;
  line-height: 1.6875rem;
  text-align: center; }

.stats__legend {
  display: block;
  font-size: 1.25rem;
  line-height: 1.25rem; }

.stats__legend--bottom {
  padding-top: 1.25rem;
  padding-bottom: 0.875rem;
  text-align: center;
  color: rgba(255, 255, 255, 0.3);
  background-color: #292929; }

.stats__legend--top {
  display: none; }

.stats__legend sup {
  top: -0.25em;
  font-size: 90%; }

.stats__list {
  width: 100%;
  border-collapse: collapse; }

.stats__list tr {
  color: #ffffff;
  background-color: #404040; }
  .stats__list tr:nth-child(2) {
    background-color: #4e4e4e; }
  .stats__list tr:nth-child(3) {
    background-color: #353535; }
  .stats__list tr:nth-child(4) {
    background-color: #404040; }

.stats__value,
.stats__field {
  width: 50%;
  padding: 1.25rem;
  padding-bottom: 1.1875rem;
  vertical-align: top; }

.stats__value {
  padding-right: 0;
  font-size: 2.5rem;
  line-height: 2.5rem;
  font-weight: bold; }

.stats__value sup {
  top: -0.35em;
  margin-left: 0.3125rem; }

.stats__field {
  padding-left: 0;
  font-size: 1.125rem;
  line-height: 1.125rem;
  text-align: right; }

@media (min-width: 48rem) {
  .stats {
    margin-top: 7%; }
  .stats__wrapper {
    width: 40rem;
    max-width: 60rem;
    margin: 0 auto; }
  .stats__header {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between; }
    .stats__header::before {
      content: "";
      position: absolute;
      top: 1.6875rem;
      left: -0.9375rem;
      border: 0.9375rem solid transparent;
      border-bottom-color: #fff;
      -webkit-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
              transform: rotate(45deg); }
  .stats__slogan {
    width: 15rem;
    margin-bottom: 1.125rem;
    font-size: 2.5rem;
    line-height: 2.5rem;
    text-align: left;
    text-transform: none; }
  .stats__intro {
    width: 13.125rem;
    margin-top: 0.3125rem;
    padding-right: 2.9375rem;
    text-align: left; }
  .stats__legend--top {
    display: block;
    width: 15rem;
    color: rgba(0, 0, 0, 0.3); }
  .stats__legend--bottom {
    display: none; }
  .stats__list tbody {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap; }
  .stats__list tr {
    width: 15.625rem;
    padding: 1.5625rem 2.1875rem;
    padding-bottom: 1.75rem; }
  .stats__list td {
    display: block;
    width: auto;
    padding: 0; }
  .stats__value {
    margin-bottom: 0.4375rem;
    font-size: 4.5rem;
    line-height: 4.5rem; }
  .stats__field {
    font-size: 1.25rem;
    line-height: 1.6875rem;
