@media (min-width: 75rem) {
  .stats {
    margin-top: 1.4375rem; }
  .stats__wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 60rem;
    margin-top: -4.1875rem; }
  .stats__header {
    display: block;
    width: 15rem; }
    .stats__header::before {
      top: 1.375rem; }
  .stats__slogan {
    width: auto;
    margin-bottom: 2.625rem; }
  .stats__intro {
    width: auto;
    margin-bottom: 2rem;
    padding-right: 1.875rem; }
  .stats__legend--top {
    width: auto; }
  .stats__list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 40rem; } }
